#textblock {
    height: 100%;
}

#textblock-container {
    width: 70%;
    margin: 0 auto;
    padding-top: 70px;
}

#textblock-title {
    color: #ffaf1b;
    font-size: 35px;
    font-weight: 600;
    font-family: "Helvetica Neue";
}

#textblock-content {
    color: #ffaf1b;
    font-size: 20px;
}


.animation,
.animation_layer {
    height: 100%;
}

.animation {
    display: block;
    position: relative;
    z-index: 10;
}

.animation_layer {
    background-position: bottom center;
    background-size: auto 1038px;
    background-repeat: repeat-x;
    width: 100%;
    position: absolute;
}

.animation_layer.parallax {
    position: fixed;
}

